import { Injectable } from "@angular/core";
import { BehaviorSubject, Observable } from 'rxjs';
import { Doctors } from "./doctors.model";
import { HttpClient, HttpErrorResponse } from "@angular/common/http";
import { UnsubscribeOnDestroyAdapter } from "src/app/shared/UnsubscribeOnDestroyAdapter";
import { environment } from "src/environments/environment";
@Injectable({providedIn: 'root'})
export class DoctorsService extends UnsubscribeOnDestroyAdapter {
  private readonly API_URL = "assets/data/doctors.json";
  isTblLoading = true;
  dataChange: BehaviorSubject<Doctors[]> = new BehaviorSubject<Doctors[]>([]);
  // Temporarily stores data from dialogs
  dialogData: any;
  constructor(private httpClient: HttpClient) {
    super();
  }
  get data(): Doctors[] {
    return this.dataChange.value;
  }
  getDialogData() {
    return this.dialogData;
  }
  /** CRUD METHODS */
  getAllDoctorss(): void {
    this.subs.sink = this.httpClient.get<Doctors[]>(`${environment.apiUrl}/getAllDoctorss`).subscribe(
      (data) => {
        this.isTblLoading = false;
        this.dataChange.next(data);
      },
      (error: HttpErrorResponse) => {
        this.isTblLoading = false;
        console.log(error.name + " " + error.message);
      }
    );
  }

  getAllDoctors() :Observable<Doctors[]>{
    return this.httpClient.get<Doctors[]>(`${environment.apiUrl}/getAllDoctorss`);
  }
  addDoctors(doctors: Doctors): void {
    this.dialogData = doctors;

     this.httpClient.post(`${environment.apiUrl}/addDoctors`, doctors).subscribe(data => {
      this.dialogData = doctors;
      },
      (err: HttpErrorResponse) => {
     // error code here
    });
  }
  updateDoctors(doctors: Doctors): void {
    this.dialogData = doctors;
    console.log(doctors);

    this.httpClient.put(`${environment.apiUrl}/updateDoctors`  + doctors.id, doctors).subscribe(data => {
      this.dialogData = doctors;
      console.log(data);
    },
    (err: HttpErrorResponse) => {
      // error code here
      console.log('Error Update Doctors');
    }
  );
  }
  deleteDoctors(id: number): void {
    console.log(id);

     this.httpClient.delete(`${environment.apiUrl}/deleteDoctors` + id).subscribe(data => {
      console.log(id);
      },
      (err: HttpErrorResponse) => {
         // error code here
      }
    );
  }
}
