import { Component, OnInit, Inject } from '@angular/core';
import { DashboardServices } from '../../../dashboard.service';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'app-delete-cita-web',
  templateUrl: './delete-cita-web.component.html',
  styleUrls: ['./delete-cita-web.component.sass']
})
export class DeleteCitaWebComponent  {

  constructor(   public dialogRef: MatDialogRef<DeleteCitaWebComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public appointmentService: DashboardServices) {
    
   }

  onNoClick(): void {
    this.dialogRef.close();
  }
  confirmDelete(): void {
    this.appointmentService.deleteCitaWeb(this.data.idCitaWeb);
    console.log('la data:' ,this.data.idCitaWeb);
  }

}
