import { Injectable } from "@angular/core";
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { UnsubscribeOnDestroyAdapter } from "src/app/shared/UnsubscribeOnDestroyAdapter";
import { environment } from "src/environments/environment";
import { BehaviorSubject } from 'rxjs';
import { citaWebModel } from './cita-web/citaWeb.model';
import { correoWebModel } from './correo-web/correoWeb.model';

@Injectable({ providedIn: 'root' })
export class DashboardServices extends UnsubscribeOnDestroyAdapter {
  isTblLoading = true;
  dialogData: any;

  dataChange: BehaviorSubject<citaWebModel[]> = new BehaviorSubject<citaWebModel[]>([]);
  dataChange2: BehaviorSubject<correoWebModel[]> = new BehaviorSubject<correoWebModel[]>([]);


  get data(): citaWebModel[] {
    return this.dataChange.value;
  }
  get data2(): correoWebModel[] {
    return this.dataChange2.value;
  }
  constructor(private httpClient: HttpClient) {
    super();
  }

  getDialogData()         {return this.dialogData;}
  getCountPatients()      {return this.httpClient.get(`${environment.apiUrl}/getCountPatients`);}
  getCountAppointments()  {return this.httpClient.get(`${environment.apiUrl}/getCountAppointments`);}
  getAllAppointments()    {return this.httpClient.get(`${environment.apiUrl}/getAllAppointments`);}
 
 
    /** CRUD METHODS */
    getAllCitaWeb(): void {
      this.subs.sink = this.httpClient.get<citaWebModel[]>(`${environment.apiUrl}/getAllCitaWeb`).subscribe(
        (data) => {
          this.isTblLoading = false;
          this.dataChange.next(data);
        },
        (error: HttpErrorResponse) => {
          this.isTblLoading = false;
          console.log(error.name + " " + error.message);
        }
      );
    }

    getAllCorreoWeb(): void {
      this.subs.sink = this.httpClient.get<correoWebModel[]>(`${environment.apiUrl}/getAllCorreoWeb`).subscribe(
        (data2) => {
          this.isTblLoading = false;
          this.dataChange2.next(data2);
        },
        (error: HttpErrorResponse) => {
          this.isTblLoading = false;
          console.log(error.name + " " + error.message);
        }
      );
    }

    deleteCitaWeb(id: number): void {
      this.httpClient.delete(`${environment.apiUrl}/deleteCitaWeb` + id).subscribe(data => {
       },
       (err: HttpErrorResponse) => {
       }
     );
   }

   deleteCorreoWeb(id: number): void {
    this.httpClient.delete(`${environment.apiUrl}/deleteCorreoWeb` + id).subscribe(data => {
     },
     (err: HttpErrorResponse) => {
     }
   );
 }
}
