import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { DashboardServices } from '../../../dashboard.service';

@Component({
  selector: 'app-delete-correo-web',
  templateUrl: './delete-correo-web.component.html',
  styleUrls: ['./delete-correo-web.component.sass']
})
export class DeleteCorreoWebComponent  {

  constructor(   public dialogRef: MatDialogRef<DeleteCorreoWebComponent>,
    @Inject(MAT_DIALOG_DATA) public data2: any,
    public appointmentService: DashboardServices) {
    
   }

  onNoClick(): void {
    this.dialogRef.close();
  }
  confirmDelete(): void {
    this.appointmentService.deleteCorreoWeb(this.data2.idCorreoWeb);
    console.log('la data:' ,this.data2.idCorreoWeb);
  }


}
