import { Page404Component } from "./../authentication/page404/page404.component";
import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { DashboardComponent } from "./dashboard/dashboard.component";
import { AppointmentsComponent } from "./appointments/appointments.component";
import { DoctorsComponent } from "./doctors/doctors.component";
import { PatientsComponent } from "./patients/patients.component";
import { SettingsComponent } from "./settings/settings.component"; 
const routes: Routes = [
  {
    path: "dashboard",
    component: DashboardComponent,
  },
  {
    path: "appointments",
    component: AppointmentsComponent,
  },
  // {
  //   path: "doctors",
  //   component: DoctorsComponent,
  // },
  {
    path: "patients",
    component: PatientsComponent,
  },
  // {
  //   path: "patients",
  //   loadChildren: () =>
  //     import("./patients-appointment/patients-appointment.module").then((m) => m.PatientsAppointmentModule),
  // },
  {
    path: "doctors2",
    loadChildren: () =>
      import("./doctors2/doctors.module").then((m) => m.DoctorsModule),
  },
  {
    path: "patients2",
    loadChildren: () =>
      import("./patients2/patients.module").then((m) => m.PatientsModule),
  },
  {
    path: "cotizaciones",
    loadChildren: () =>
      import("./cotizaciones/cotizaciones.module").then((m) => m.CotizacionesModule),
  },
  {
    path: "proveedores",
    loadChildren: () =>
      import("./proveedores/proveedores.module").then((m) => m.ProveedoresModule),
  },
  {
    path: "appointment2",
    loadChildren: () =>
      import("./appointment2/appointment.module").then((m) => m.AppointmentModule),
  },
  {
    path: "settings",
    component: SettingsComponent,
  },
  { path: "**", component: Page404Component },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class DoctorRoutingModule {}
